body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

p {
    line-height: 0.5;
}


/* Phần Item*/
.wrapper-item {
    padding-left: 10px;
    padding-right: 10px;
}

.item:hover {
    opacity: 0.7;
    box-shadow: -1px 1px 5px 5px gray;
}

.item .a_item {
    text-decoration: none;
    color: black;
}
/* --------------------------------------- */




/* TRANG CHỦ PAGE */
/* Phần Quảng Cáo */
#itemQuangCao:hover {
    opacity: 0.9;
}

.navMenu{
    color: black;
}
/* --------------------------------------- */

/* Phần Slider */
.post-slider {
    width: 100%;
    position: relative;
}

.post-slider .slider-title {
    text-align: center;
    margin: 30px auto;
}

.post-slider .post-wrapper {
    width: 85%;
    height: 220px;
    margin: 0px auto;
    overflow: hidden;
}

.post-slider .post-wrapper .post {
    display: inline-block;
    margin: 10px;
    height: 200px;
    width: 220px;
    background: white;
}

.slider-image {
    width: 100%;
    height: 150px;
}

.post-slider .post-wrapper .post .post-info {
    text-align: center;
    padding-top: 15px;
}

.post-slider .post-wrapper .post:hover {
    opacity: 0.7;
    box-shadow: -1px 1px 5px 5px gray;
}

.post-slider .next {
    position: absolute;
    top: 60%;
    right: 50px;
    color: black;
}

.post-slider .prev {
    position: absolute;
    top: 60%;
    left: 50px;
    color: black;
}
/* --------------------------------------- */
/* -------------------------------------------------------------------- */




/* SẢN PHẨM PAGE */
/* Phần sidebar của trang Sản Phẩm */
.danhmucchinh-sidebar {
    padding-top: 5px;
}

.danhmucphu-sidebar {
    padding-left: 20px;
}

.danhgia-4sao-sidebar {
    margin-top: 3px;
}

.danhgia-3sao-sidebar {
    margin-top: 3px;
}

.thuonghieu-items-sidebar {
    padding-top: 5px;
    padding-left: 20px;
}

.nhacungcap-items-sidebar {
    padding-top: 5px;
    padding-left: 20px;
}
/* --------------------------------------- */

/* Phần main của trang Sản Phẩm */
.pagination-maincontent {
    margin-top: 30px;
}
/* --------------------------------------- */
/* -------------------------------------------------------------------- */

/* SẢN PHẨM DETAIL PAGE*/
.showimg_big {
    width: 450px;
    height: 400px;
}

.showimg_little {
    width: 100px;
    height: 100px;
    margin-left: 10px;
}

.img_little {
    width: 98px;
    height: 98px;
}

.img_little:hover{
    box-shadow: 0 2px 5px black;
}

.infor-item {
    border: 10px solid white;
    padding: 20px;
}

/* Phần custom input number (số lượng) trong Sản Phẩm Detail*/
input[type="number"] {
    border: none;
    outline: none;
    padding: 5px;
    border-radius: 5px;
    font-weight: bold;
    box-shadow: 0 2px 5px black;
    margin-left: 5px;
    margin-right: 5px;
}

input[type="number"]:hover,
input[type="number"]:focus {
    background: orange;
    color: white;
}

.infor-item button {
    background: dodgerblue;
    color: white;
    font-weight: bold;
    border: none;
    outline: none;
    padding: 0 10px;
    border-radius: 10px;
    width: 40px;
}

.infor-item button:hover {
    background: orange;
}
/* --------------------------------------- */

.infor-detail-item{
    background-color: #F8F9FA;
    width: 100%;
    height: 300px;
    border-radius: 5px;
}

.infor-detail-item .table-infor-detail-item{
    background: white;
    justify-content: center;
}

.infor-detail-item .table-infor-detail-item .title{
    background: lightgrey;
    width: 200px;
    font-size: small;
    font-weight: bold;
}

.description-item{
    background-color: #F8F9FA;
    width: 100%;
    height: auto;
    border-radius: 5px;
}

.description-item .description-item-img{
    width: 700px;
    align-items: center;
    justify-content: center;
    margin-left: 50px;
    margin-bottom: 40px;
}

.description-item .wrapper-description-item-content{
    padding: 50px;
}
/* -------------------------------------------------------------------- */